<template>
<div class="Cooperative-enterprise">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Company_number")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Company_name")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="7">
        <p>{{$t("common.Cooperation_status")}}&nbsp</p>
        <Select style="width:120px;margin-right:10px;">
            <Option v-for="item in cityList" :value="item.value" :label="item.label" :key="item.value"></Option>
        </Select>
        <Button type="primary">{{$t("common.Query")}}</Button>
      </Col>
      <Col :xs="12" :sm="12" :lg="5" style="line-height: 6;">
        <Button type="primary" @click="goto('Add_company')">{{$t("common.Add_enterprise")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="center" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'Cooperative_enterprise',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            rom:null,
            cityList: [
                {
                    value: 'All',
                    label: this.$t('common.All')
                },
                {
                    value: 'Yes',
                    label: this.$t('common.In_cooperation')
                },
                {
                    value: 'No',
                    label: this.$t('common.Terminated')
                }
            ],
            columns1: [
                {
                    title:this.$t('common.Company_number'),
                    key: 'company_number',
                    align:'center'
                },
                {
                    title:this.$t('common.Company_name'),
                    key: 'company_name',
                    align:'center'
                },
                {
                    title:this.$t('common.Rating_status'),
                    key: 'grade_state',
                    align:'center'
                },
                {
                    title:this.$t('common.Corporate_credit_rating'),
                    key: 'level',
                    align:'center'
                },
                {
                    title:this.$t('common.Rating_application_time'),
                    key: 'time',
                    align:'center'
                },
                {
                    title:this.$t('common.Cooperation_status'),
                    key: 'cooperative_state',
                    align:'center'
                },
                {
                    title:this.$t('common.Operate'),
                    key: 'operate',
                    align:'center',
                    render: (h, params) => {
                      return h('div', [
                          h('Button', {
                              props: {
                                  type: params.row.operate == this.$t('common.Renewal')?'info':'warning',
                                  size: 'small'
                              },
                              style: {
                                  marginRight: '10px'
                              },
                              on: {
                                  click: () => {
                                    if(params.row.operate == this.$t('common.Renewal')){
                                      this.$router.push({
                                          path: '/Company_details',
                                          name: 'Company_details',
                                          params: {
                                              name: 'Renewal',
                                              send_data: 'Renewal'
                                          }
                                      })
                                    }else{
                                      this.$router.push({
                                          path: '/Company_details',
                                          name: 'Company_details',
                                          params: {
                                              name: 'Pause',
                                              send_data: 'Pause'
                                          }
                                      })
                                    }
                                  }
                              }
                          }, params.row.operate)
                      ]);
                  }
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null
        }
    },
    methods:{
      goto (index){
        this.$router.push('/'+index);
      },
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      oneandtow (){
        this.rom =  parseInt(Math.random()*(3-1)+0)>0
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              this.oneandtow ()
              data.push({
                  company_number: 'DG2018060300'+i,
                  company_name: parseInt(Math.random()*(3-1)+0)>0?'北京市建筑投资有限公司':'福建六建集团有限公司',
                  grade_state: this.$t('common.Already_rated'),
                  level:parseInt(Math.random()*(3-1)+0)>0?'AAA':'AA',
                  time:this.formatDate(this.getRandomDateBetween()),
                  cooperative_state:this.rom>0? this.$t('common.In_cooperation') : this.$t('common.Terminated') ,
                  operate:this.rom>0 ? this.$t('common.Pause') : this.$t('common.Renewal')
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      getMoble() {
          var prefixArray = new Array("130", "131", "132", "133", "135", "137", "138", "170", "187", "189");
          var i = parseInt(10 * Math.random());
          var prefix = prefixArray[i];

          for (var j = 0; j < 8; j++) {
              prefix = prefix + Math.floor(Math.random() * 10);
          }
          return prefix
      },
      onedata (data,index){
        //console.log(data,index)
        //this.$router.push('/Company_details');
      },
      sendParams () {
          this.$router.push({
              path: '/Userinfo',
              name: 'Userinfo',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       }
    },
    created(){
         this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.Cooperative-enterprise{
  
}
.Cooperative-enterprise .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.Cooperative-enterprise .search_criteria p{
  height: 30px;
}
.Cooperative-enterprise .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  text-align: center;
  background-color: #fff;
}
</style>



